.form {
  margin: auto;
  color: black;
  display: flex;
  max-width: 650px;
  flex-direction: column;
  align-items: center;
}

.form label {
  display: block;
  margin-top: 5px;
  width: 100%;
}

.form input[type="text"],
.form input[type="number"] {
  box-sizing: border-box;
  display: block;
  padding: 10px 5px;
  border: 1px solid #555;
  border-radius: 5px;
  background-color: #444;
  color: #fff;
  font-size: 22px;
  width: 100%;
  flex: 1;
}

.form .btn {
  margin-top: 10px;
}
